@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap);
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 80px;
  z-index: 999;
  background-image: linear-gradient(to top, rgba(255, 255, 255, 0), #f8f8f8e8, var(--white));
  /* padding: 10px 0px; */
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 24px;
  max-width: 1500px;
  margin: 0;
}

.navbar-logo {
  font-weight: bold;
  justify-self: start;
  font-family: var(--title);
}

.navbar-logo-img {
  height: 100px;
  width: auto;
}

/* .fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.5rem;
} */

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: auto;
  justify-content: end;
  margin: 0;
}

.nav-item {
  margin: 0;
}

.nav-links {
  color: var(--black);
  display: flex;
  align-items: right;
  text-decoration: none;
  margin: 0 0 25px 50px;
  font-weight: medium;
  font-size: 18px;
  font-family: var(--title);
}

.nav-links:hover {
  color: var(--hover);
  border-bottom: 4px groove var(--accent);
  transition: all 0.1s ease-out;
}

.fa-bars {
  color: var(--black);
  font-size: 24px;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

ul{
  list-style: inside;
  padding-left: 0;
}

.active {
  font-size: 18px;
  color: var(--accent);
}
  
@media screen and (max-width: 500px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    /* height: 90vh; */
    position: absolute;
    /* top: 80px; */
    left: 100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background-color: var(--white);
    left: 0;
    right:0;
    top: 40px;
    padding-bottom: 20px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    /* padding: 2rem; */
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: var(--white);
    color: var(--accent);
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    font-size: 24px;
    cursor: pointer;
  }

  .fa-times {
    color: var(--black);
    font-size: 24px;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: var(--white);
    padding: 14px 20px;
    border: 1px solid var(--white);
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: var(--black);
    color: var(--accent);
    transition: 250ms;
  }
}
* {
  box-sizing: content-box;
  margin: 0px;
}

:root {
  /* define variables */
  --accent: #7e71d3; /* #c51d2e or #ea9554 */
  --accent-light: #7e71d36c;
  --accent-lighter: #7e6bfa2f;
  --white: #f8f8f8;
  --black: #242424;
  --gray: #959595;
  --hover: #C4C4C4;
  --pop-up: #83838380;
  --title: "Inter";
  --title-fallback: sans-serif;
  --title-resume: "Helvetica";
  --body: "Inter";
  --body-fallback: serif;
}

.background {
  background-color: #f8f8f8;
  background-color: var(--white);
}

.page-wrapper {
  padding: 0px 50px;
  background-color: #f8f8f8;
  background-color: var(--white);
  max-width: 1100px;
  margin: auto;
}

/* customize appearance of text */
h1 {
  font-family: "Inter", sans-serif;
  font-family: var(--title), var(--title-fallback);
  font-size: 72px;
  font-weight: bolder;
}

h2 {
  font-family: "Inter", sans-serif;
  font-family: var(--title), var(--title-fallback);
  font-size: 30px;
  font-weight: bold;
}

h3 {
  font-family: "Inter", sans-serif;
  font-family: var(--title), var(--title-fallback);
  font-size: 24px;
  font-weight: bold;
  padding-left: 0px;
}

h4 {
  font-family: "Inter", sans-serif;
  font-family: var(--title), var(--title-fallback);
  font-weight: normal;
  font-size: 20px;
}

h5 {
  font-family: "Inter", sans-serif;
  font-family: var(--title), var(--title-fallback);
  font-weight: lighter;
  font-size: 14px;
  color: #959595;
