.hovertext {
    position: relative;
}

.hovertext:before {
    content: attr(data-hover);
    visibility: hidden;
    opacity: 0;
    background-image: linear-gradient(to top, var(--white), var(--white), rgba(0, 0, 0, 0));
    padding: 30px 15px 15px 15px;
    box-sizing: border-box;
    transition: opacity 1s ease-in-out;
    width: 100%;

    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 0;
    font-family: var(--title), var(--title-fallback);
}

.hovertext:hover:before {
    opacity: 1;
    visibility: visible;
}
.project-card-container {
    color: black;
