/* AlignedSection style sheet */
/* customize appearance of text */

/* font style for Erica Fu */
.name {
    font-family: var(--title-resume), var(--title-fallback);
    font-weight: bold;
}

/* font style for capitalized titles on the left */
.capitalized {
    font-family: var(--title-resume), var(--title-fallback);
    font-weight: bold;
    text-transform: uppercase;
}

/* bolded title text */
.bolded-title {
    font-family: var(--title-resume), var(--title-fallback);
    font-weight: bold;
    padding-left: 0px;
}

/* faded title text */
.faded-title, th {
    font-family: var(--title-resume), var(--title-fallback);
    font-weight: normal;
    color: gray;
}

/* for dates, content, and location text */
.dates, .contact, td{
    font-family: var(--title-resume), var(--body-fallback);
}

.right-section>a {
    text-decoration: none;
    color: var(--black);
}

.link:hover {
    /* background-image: linear-gradient(to right, var(--white), var(--accent-light)); */
}

.same-line {
    display: inline;
    overflow: auto;
}

.location {
    float: right;
    font-family: var(--title-resume), var(--body-fallback);
}

ul {
    list-style-position: outside;
    margin-left: 20px;
}
.right-section li {
    font-family: var(--title-resume), var(--body-fallback);
}

table {
    width: 100%;
}
td, th {
    text-align: left;
}

/* mobile version: */
@media screen and (min-width: 0px) {
    /* customize appearance of text */

    /* font style for Erica Fu */
    .name {
        font-size: 22px;
    }

    /* font style for capitalized titles on the left */
    .capitalized {
        font-size: 18px;
    }

    /* bolded title text */
    .bolded-title {
        font-size: 14px;
    }

    /* faded title text */
    .faded-title, th {
        font-size: 14px;
    }

    /* for dates, contact, and location text */
    p, td, .dates, .contact{
        font-size: 12px;
    }

    .location {
        font-size: 12px;
    }

    li {
        font-size: 12px;
    }

    /* section formatting */
    .aligned-section {
        padding: 10px 0px;
    }

    .left-section {
        padding: 0px 10px;
    }
    .right-section {
        padding: 0px 10px;
    }
}

/* laptop version: */
@media screen and (min-width: 700px) {
    /* font style for Erica Fu */
    .name {
        font-size:28px;
    }

    /* font style for capitalized titles on the left */
    .capitalized {
        font-size: 20px;
    }

    /* bolded title text */
    .bolded-title {
        font-size: 18px;
    }

    /* faded title text */
    .faded-title, th {
        font-size: 18px;
    }

    /* for dates, content, and location text */
    p, td, .dates, .location, li, .contact {
        font-size: 14px;
    }
    
    /* formatting for sections */
    .aligned-section {
        display: flex;
    }
    
    .left-section, .right-section {
        padding: 0px 10px;
    }
    .left-section {
        flex: 0 0 30%;
        max-width: 300px;
        text-align: right;
    }
    .right-section {
        flex: 0 0 60%;
    }
}
